* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
.Card {
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-image: url(C:\Users\Aman\Desktop\React-Course-Projects\react-course\public\milin-john-We01ED9Ei6g-unsplash.jpg);
  background-size: cover; */
  background: #bdc3c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2c3e50,
    #bdc3c7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c3e50,
    #bdc3c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  opacity: 1;
  height: 100vh;
}
.Container {
  border-radius: 20px;
  width: 40%;
  height: 700px;
  text-align: center;
  background-image: url(C:\Users\Aman\Desktop\React-Course-Projects\react-course\public\milin-john-We01ED9Ei6g-unsplash.jpg);
  background-size: cover;
  color: #fff;
  position: relative;
}
.Container-right {
  width: 20%;
  height: 700px;
  border-radius: 20px;
  text-align: center;
  background-color: #fff;
  opacity: 0.7;
  color: #000;
  position: relative;
}
.weather-title {
  position: absolute;
  top: 30%;
  left: 35%;
  font-size: 2.4rem;
}
hr {
  position: absolute;
  top: 40%;
  left: 10%;
  width: 80%;
  color: #fff;
}
.hr {
  width: 100%;
  color: #fff;
}
.Search-bar {
  display: flex;
  position: absolute;
  top: 45%;
  left: 10%;
  margin-left: 14px;
}
.Search-btn {
  height: 2rem;
  width: 2rem;
  background-color: #fff;
  color: #000;
  cursor: pointer;
  padding: 5px;
  border-radius: 20px;
  transition: 0.3s;
}
.Search-btn:hover {
  background-color: #000;
  color: #fff;
}
.input-search {
  height: 2rem;
  width: 80%;
  border-bottom-color: #000;
  border-radius: 17px;
  color: #000;
  padding: 9px;
  margin-right: 5px;
  background-color: #fff;
}
.current-location {
  position: absolute;
  right: 0%;
  top: 5%;
  font-size: 2.7rem;
  padding: 10px;
  margin-right: 20px;
}
.current-location span {
  display: block;
  font-size: 2rem;
}
.wrapper-date-cel {
  position: absolute;
  bottom: 0%;
  left: 10%;
}
.wrapper {
  display: flex;
  width: 40%;
  gap: 160px;
  justify-content: space-between;
  align-items: center;
}
.time {
  font-size: 2.7rem;
  color: #fff;
  font-weight: 500;
  width: 200px;
}
.day-month-year {
  margin-bottom: 20px;
  letter-spacing: 2px;
  font-weight: 500;
}
.Celsuis {
  margin-bottom: 20px;
  padding: 10px;
  float: left;
  font-size: 2.7rem;
  font-weight: 500;
}
.city-name {
  font-size: 1.7rem;
  color: #000;
  text-align: center;
  position: absolute;
  top: 52%;
  left: 15%;
}
.icon {
  font-size: 4.2rem;
  margin-top: 30px;
}
.weather-detail {
  margin-top: 370px;
}
.weather {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 6px;
  font-size: 1.2rem;
}

@media (max-width: 767px) {
  .Container {
    width: 100%;
    height: 100%;
    overflow-y: hidden;
  }
  .current-location {
    font-size: 2.3rem;
  }
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .wrapper-date-cel {
    position: absolute;
    bottom: 0%;
    left: 5%;
  }
  .time {
    position: absolute;
    bottom: 30%;
    width: 200px;
    left: 5%;
  }
  .day-month-year {
    position: absolute;
    bottom: 0%;
    margin-top: 10px;
  }
  .Celsuis {
    margin-bottom: 0px;
    padding: 10px;
    font-size: 2rem;
    width: 230px;
    margin-top: 3rem;
    margin-left: 80px;
  }
  .Container-right {
    width: 80%;
    height: 600px;
    position: absolute;
    top: 12%;
  }
  .weather-detail {
    margin-top: 200px;
  }
  .city-name,
  .weather-title {
    position: relative;
    top: auto;
    left: auto;
  }
  .current-location {
    top: 2%;
    margin-right: 40px;
  }
  .input-search {
    width: 100%;
    padding: 15px;
  }
  .Date {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 200px;
  }
}
@media (max-width: 375px) and (max-height: 667px) {
  .weather-detail {
    margin-top: 130px;
  }
  .Container-right {
    width: 80%;
    height: 500px;
  }
  .time {
    position: absolute;
    bottom: 20%;
    font-size: 2rem;
  }
  .Celsuis {
    margin-bottom: 0px;
    padding: 10px;
    font-size: 1.5rem;
    width: 280px;
    margin-left: 59px;
    margin-top: 5rem;
  }
}
